body {
  background: linear-gradient(to bottom, #2a0066, #080013);
  color: white;
  margin: 0;
  font-family: Arial, sans-serif;
}

/* Container */
.player-wrapper {
  width: 400px;
  margin: 60px auto;
  padding: 40px;
  background: rgba(0, 0, 0, 0.35);
  border-radius: 25px;
  text-align: center;
  box-shadow: 0 8px 20px rgba(0,0,0,0.5);

  /* NEW: mobile + footer safe */
  max-width: 90%;
  box-sizing: border-box;
}

/* Titles */
.now-playing {
  margin: 0;
  font-size: 20px;
  opacity: 0.8;
}

.album-name {
  margin-top: 5px;
  font-size: 14px;
  color: #a6ffea;
}

/* Big icon */
.big-icon {
  font-size: 90px;
  margin: 20px 0;
  color: #c59cff;
}

/* Song text */
.song-title {
  font-size: 30px;
  margin: 10px 0;
}

.song-artist {
  font-size: 16px;
  opacity: 0.8;
}

/* Controls */
.controls {
  display: flex;
  justify-content: center;
  margin-top: 25px;
  gap: 10px;
}

.play-btn {
  padding: 12px 30px;
  border-radius: 40px;
  background: linear-gradient(90deg, #ff39b3, #00e5ff);
  border: none;
  color: black;
  font-size: 18px;
  cursor: pointer;
}

.play-btn:hover {
  opacity: 0.85;
}

.skip-btn {
  background: none;
  border: none;
  color: #ccc;
  font-size: 25px;
  cursor: pointer;
}

.skip-btn:hover {
  color: white;
}

/* Equalizer bars */
.equalizer {
  margin-top: 35px;
  height: 50px;
  display: flex;
  justify-content: center;
  gap: 6px;
}

.equalizer span {
  width: 8px;
  background: #ff77d4;
  height: 10px;
  border-radius: 4px;
  animation: bounce 1s infinite ease-in-out alternate;
}

.equalizer span:nth-child(2) { animation-delay: .2s; }
.equalizer span:nth-child(3) { animation-delay: .4s; }
.equalizer span:nth-child(4) { animation-delay: .6s; }
.equalizer span:nth-child(5) { animation-delay: .8s; }

@keyframes bounce {
  from { height: 10px; }
  to { height: 50px; }
}

.pause-btn {
  padding: 12px 30px;
  border-radius: 40px;
  background: linear-gradient(90deg, #ff39b3, #00e5ff);
  border: none;
  color: black;
  font-size: 18px;
  cursor: pointer;
}

.pause-btn:hover {
  opacity: 0.85;
}

/* üì± MOBILE RESPONSIVE FIXES */
@media (max-width: 480px) {
  .player-wrapper {
    padding: 25px;
    margin: 30px auto;
    border-radius: 20px;
  }

  .song-title {
    font-size: 24px;
  }

  .play-btn,
  .pause-btn {
    width: 100%;
    font-size: 16px;
    padding: 10px;
  }

  .skip-btn {
    font-size: 22px;
  }

  .equalizer {
    height: 40px;
  }
}
